import { Link, useLocation } from 'react-router-dom';

const navItems = [
  { name: "Home", path: "/" },
  { name: "Gamified Achievements", path: "/achievements" },
  { name: "My Drives", path: "/my-drives" },
  { name: "Volunteer Guidance", path: "/guidance" },
  { name: "Community Feed", path: "/feed" },
  { name: "Profile & Settings", path: "/profile" },
];

export default function Sidebar() {
  const location = useLocation();

  return (
    <aside className="w-64 h-screen bg-gray-800 text-white fixed top-0 left-0 flex flex-col">
      <div className="text-2xl font-bold p-4 border-b border-gray-700">ShoreCare</div>
      <nav className="flex-1 p-2 space-y-2">
        {navItems.map(({ name, path }) => (
          <Link
            key={name}
            to={path}
            className={`block px-4 py-2 rounded hover:bg-blue-600 ${
              location.pathname === path ? "bg-blue-700" : ""
            }`}
          >
            {name}
          </Link>
        ))}
      </nav>
    </aside>
  );
}
